*{
  box-sizing: border-box;
  font-family: 'Press Start 2P', cursive;
}

body{
  background-color: #e6d2a3;
}

.container{
/*   background-color: #13278f; */
  background-color: #e6d2a3;
  width: 100%;
  margin: 0 auto;
  padding: 30px 0 0 0;
}

h1{
  text-align: center;
  width: 600px;
  font-size: 37px;
  height: 60px;
  color: #fff7f7;
  padding: 10px;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 80px;
  border: 5px solid #fff7f7;
  border-radius: 30px;
}

.counters{
  display: flex;
  flex-direction: row;
  justify-content:  center;
}

.counter-container{
  width: 300px;
  padding: 0 20px;
}

.counter-container h5{
  text-align: center;
  font-size: 27px;
  font-weight: 300;
  color: #fff7f7;
  background-color: #870000;
  margin: 0;
  padding: 3px 0;
}

.counter{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
/*   background-color: #fff7f7; */
  padding-top: 5px;
}

.counter p{
  display: block;
  text-align: center;
  margin: 0 17px;
  font-size: 40px;
  color: black;
} 

.counter button{
  height: 35px;
  width: 35px;
  border: none;
  background-color: black;
  font-size: 25px;
  color: #fff7f7;
  text-align: center;
}

.counter button:hover{
  background-color: #fff7f7;
  color: black;
}

.timer{
  background-color: #870000;
  width: 350px;
  margin: 0 auto;
  margin-top: 60px;
  padding: 10px;
}

.timer h5, .timer p{
  text-align: center;
}

.timer h5{
  font-size: 35px;
  color: #fff7f7;
  margin: 10px 0 10px 0;
}

.timer p{
  font-size: 55px;
  color: #cc8f00;
  margin: 0 0 10px 0;
}

.play-buttons{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.play-buttons button{
  border: none;
  background-color: transparent;
  font-size: 13px;
  color: black;
  margin: 5px 20px;
}

.play-buttons button:hover{
  color: #fff7f7;
}

button:hover{
  cursor: pointer;
}

@media(max-width: 600px){
  h1{
    width: 350px;
    font-size: 20px;
    height: 50px;
    line-height: 25px;
    border: 4px solid #fff7f7;
  }
}

@media(max-width: 400px){
  .container{
    padding-top: 10px;
  }
  h1{
    width: 230px;
    font-size: 13px;
    height: 40px;
    line-height: 15px;
    border: 3px solid #fff7f7;
    margin-top: 10px;
    margin-bottom: 30px;
  }
  
  .counters{
    flex-direction: column;
  }
  
  .counter-container{
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
  }

  .counter-container h5{
    text-align: center;
    font-size: 29px;
    font-weight: 300;
    color: #fff7f7;
    background-color: #870000;
    margin: 0;
    padding: 3px 0;
  }
  
  .timer{
  width: 100%;
  margin: 0 auto;
  margin-top: 30px;
  padding: 10px;
}

  .timer h5, .timer p{
    text-align: center;
  }

  .timer h5{
    font-size: 35px;
    color: #fff7f7;
    margin: 10px 0 10px 0;
  }

  .timer p{
    font-size: 55px;
    color: #cc8f00;
    margin: 0 0 10px 0;
  }
}